@charset "UTF-8";
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0; }

a, a:hover {
  text-decoration: none; }

ul {
  list-style: none; }

/* color: #FF002E; */
/* color: #A7FE02  зелёный */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

body {
  display: flex;
  justify-content: space-between;
  background: #232C32; }

.nav {
  width: 200px;
  height: 100%;
  background: #0F181F;
  position: fixed;
  z-index: 99;
  display: flex;
  flex-direction: column; }
  .nav-logo__link {
    display: flex;
    justify-content: center;
    padding-top: 10px;
    align-items: center; }
  .nav-logo__img {
    width: 50px;
    height: 37px; }
  .nav-logo__text {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 13px;
    color: #fff;
    margin-left: 5px;
    display: flex;
    flex-direction: column;
    text-align: center; }
    .nav-logo__text span {
      color: #fff; }
  .nav ul {
    padding-top: 30px; }
  .nav li div {
    display: flex;
    align-items: center; }
  .nav li a {
    display: block;
    padding: 10px 10px 10px 25px;
    margin: 10px 0 10px 0;
    position: relative;
    font-family: Montserrat;
    font-weight: 400;
    font-size: 20px;
    color: #fff;
    text-decoration: none; }
    .nav li a:hover {
      font-family: "Montserrat";
      font-weight: 400;
      background: #1A232A;
      color: #FF002E;
      box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.3); }
    .nav li a::after {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: 5px;
      height: 0;
      opacity: 0;
      background-color: #FF002E;
      z-index: 1;
      -webkit-transition: all 200ms linear;
      transition: all 200ms linear; }
    .nav li a:hover:after {
      height: 100%;
      opacity: 1;
      top: 0; }
    .nav li a .fa {
      font-size: 30px; }
  .nav .item {
    margin-left: 25px; }

.nav-top {
  width: 100%;
  height: 60px;
  background: #1A232A;
  position: fixed;
  display: flex;
  align-items: center;
  z-index: 98; }
  .nav-top .navTopBar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 230px; }
    .nav-top .navTopBar__toggle {
      width: 70px;
      display: flex;
      justify-content: space-between; }
      .nav-top .navTopBar__toggle a {
        width: 35px;
        height: 35px;
        color: #fff;
        font-size: 35px;
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center; }
        .nav-top .navTopBar__toggle a:hover {
          color: #FF002E; }
        .nav-top .navTopBar__toggle a:active {
          color: #A7FE02; }
    .nav-top .navTopBar__search-form form {
      border-bottom: 2px solid #0f181f; }
      .nav-top .navTopBar__search-form form .search {
        width: 310px;
        height: 28px;
        background: #262F38;
        font-size: 16px;
        color: #fff;
        border: none;
        outline: none;
        padding-left: 10px; }
      .nav-top .navTopBar__search-form form .btn {
        width: 40px;
        height: 30px;
        background: #0F181F;
        border: none;
        color: #fff;
        font-size: 16px;
        margin-left: -3.6px;
        cursor: pointer;
        outline: none; }
        .nav-top .navTopBar__search-form form .btn:hover {
          color: #FF002E; }
    .nav-top .navTopBar__profil {
      width: 230px;
      display: flex;
      align-items: center;
      cursor: pointer; }
      .nav-top .navTopBar__profil-photo {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        /* background: #fff; */
        -moz-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
        -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
        box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5); }
        .nav-top .navTopBar__profil-photo img {
          position: relative;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          z-index: -2; }
      .nav-top .navTopBar__profil-name {
        margin-left: 12px; }
        .nav-top .navTopBar__profil-name h5 {
          font-family: Montserrat;
          font-weight: 600;
          font-size: 14px;
          color: #fff; }
        .nav-top .navTopBar__profil-name p {
          font-family: Montserrat;
          font-weight: 400;
          font-size: 12px;
          color: #fff;
          opacity: .6; }

main {
  width: 100%;
  height: 100%;
  margin: 60px 0 0 200px;
  /* border: 1px solid red; */ }

.aside {
  flex: 0 0 240px;
  height: 100%;
  display: block;
  margin-top: 60px; }
  .aside .advertising {
    width: 100%;
    height: 300px;
    background: #000; }
  .aside .last-music {
    width: 100%;
    background: #1A232A; }
  .aside .top {
    width: 100%;
    background: #1A232A;
    margin-top: 5px; }
  .aside h1 {
    font-family: Montserrat;
    font-weight: 400;
    font-size: 22px;
    color: #fff;
    text-align: center;
    padding-top: 10px; }
  .aside a {
    display: flex;
    align-items: center;
    padding: 10px 0 10px 15px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3); }
    .aside a:hover {
      /* background: #516472; */
      background: rgba(255, 255, 255, 0.1);
      border-bottom: 1px solid rgba(141, 18, 44, 0.3); }
    .aside a img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.75); }
  .aside .music-name {
    margin-left: 15px;
    display: flex;
    flex-direction: column; }
  .aside h5 {
    font-family: Montserrat;
    font-weight: 400;
    font-size: 18px;
    color: #fff; }
  .aside p {
    font-family: Montserrat;
    font-weight: 300;
    font-size: 13px;
    color: #fff;
    opacity: .5; }

.activePlayer {
  display: block !important; }

.b-layer {
  display: none;
  position: fixed;
  width: 100%;
  height: 60px;
  z-index: 100;
  /* border: 1px solid red; */
  bottom: 0; }

.player {
  width: 100%;
  height: 100%;
  background: #000; }

.control {
  width: 200px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto; }

.control .btn {
  cursor: pointer; }

.control .fa {
  font-size: 26px;
  color: #fff; }
